@charset "utf-8";

$soft-black: #1a1a1a;

$background-color: lighten($soft-black, 7%);
$second-background: #1a1a1e;
$panel-color: lighten($second-background, 10%);
$text-color: #f2f2f2;
$brand-color: #00e6e6;
$second-color: #3bc47f;  // #3bc45f;
$grey-color: #666;

$panel-inner-width: 1600px;
$content-inner-width: 800px;
$padding-gap: 10px;
$margin-gaps: 35px 0;
$centered: 35px auto;

$main-font: source_sans_pro, Open Sans, sans-serif;
$code-font: source_code_pro, Consolas, monaco, monospace;
@font-face {
	font-family: source_sans_pro;
	src: url(fonts/Source_Sans_Pro/SourceSansPro-Regular.ttf);
}
@font-face {
	font-family: source_code_pro;
	src: url(fonts/Source_Code_Pro/SourceCodePro-Regular.ttf);
}

@import
    "base",
    "style",
	"items",
    "animate"
;
